@import url(https://fonts.googleapis.com/css2?family=Audiowide&family=ZCOOL+KuaiLe&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: white;
  background-color: lightgray;
}

a { text-decoration: none; color: white; transition: all 0.2s; }
a:hover { color: lightgoldenrodyellow; }

header,footer {
  height: 54px;
  background-color: #333;
}

header > a {
  display: flex; align-items: center; height: 100%; margin: 0 10px;
}
header > a > img { width: 40px; margin-right: 4px;}

main { min-height: calc(100vh - 108px);}

section, aside {
  padding: 4px 12px;
}

#sec-disclaimer {
  background-color: lightsteelblue;
}

footer { display: flex; flex-direction: column; align-items: center; justify-content: center;}

.b1 { font-weight: bold; font-family: Audiowide;}

.channels {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  grid-gap: 1rem;
  gap: 1rem;
}

.articles {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
  grid-gap: 1rem;
  gap: 1rem;
}

.channel-item {
  padding: 8px 12px;
  box-shadow: 2px 3px 4px 1px #66666650;
  display: flex;
  border-radius: 8px;
  background-color: rgb(93, 196, 139);
}
.channel-item:hover { transform: scale(1.02)}
.channel-item img {
  width: 50px;
  border-radius: 8px;
  margin-right: 8px;
  opacity: 0.9;
}

.article-list-item {
  padding: 8px 12px;
  box-shadow: 2px 3px 4px 1px #66666650;
  display: flex;
  border-radius: 8px;
  background-color: rgb(84, 170, 250);
}

.article-list-item:hover { transform: scale(1.01)}
.article-list-item img {
  width: 50px;
  border-radius: 8px;
  margin-right: 8px;
  opacity: 0.9;
}

/* .remarks {
  display: flex;
} */
.tag {
  padding: 3px 8px;
  border-radius: 8px;
  text-align: center;
  vertical-align: middle;
  margin-right: 4px;
}

